import axios from 'axios';
import LaunchApp from '../../components/LaunchApp.vue';
import FooterDownload from '../../components/FooterDownload.vue';

var mySwiper;

export default {
    name: 'Post',
    components: {
        LaunchApp,
        FooterDownload
    },
    filters: {
        formatTime(value) {
            return moment.utc(value).local().format('YYYY-MM-DD HH:mm');
        }
    },
    mounted() {
        this.getBanner();
        this.getPostDetail();
        this.getCountryCode();
        this.shareid = this.$util.getUrlParam('shareid');
    },
    data() {
        return {
            clientWidth: document.body.clientWidth || document.documentElement.clientWidth,
            moment: '',
            comments: [],
            momentsData: '',
            countryCode: 'CN',
            picWidth: 0,
            picWidth66: 0,
            shareid: '',
            banner: '',
            videoReady: false,
            paramsForApp: '',
            messages: {
                en: {
                    tweebaa: 'Tweebaa',
                    views: "Views",
                    likes: 'Likes',
                    comments: 'Comments',
                    topComments: 'New Comments',
                    topPost: 'Top Post',
                    hi: "Hi,I'm ",
                    tips: 'You can also create great content',
                    downloadApp: 'Download Tweebaa App Get T-beans',
                    platform: 'Tweebaa is the world\'s 1st multi-dimensional earning commerce and value-exchanging social networking platform allowing everyone to transform their knowledge, skills, interests, and experiences into creative products and discover their hidden values.',
                },
                zh: {
                    tweebaa: '推易吧',
                    views: '浏览',
                    likes: '赞',
                    comments: '评论',
                    topComments: '最新留言',
                    topPost: '他（她）的热门瞬间',
                    hi: "Hi,我是 ",
                    tips: '你也能创造精彩瞬间',
                    downloadApp: '下载推易吧App，获取T豆奖励',
                    platform: '推易吧是全球首个支持创新变现的价值社交平台，您的知识、爱好、经验、影响力等个体价值可以在这里直接变现。',
                },
            }
        };
    },

    methods: {
        getBanner() {
            let i = Math.floor(Math.random() * 4) + 1;

            try {
                // this.banner = require('../../image/banner' + i + '-' + this.$util.language + '.jpg');
                this.banner = require('../../image/banner' + 1 + '-' + this.$util.language + '.jpg');
            } catch (e) {

            }

        },
        getCountryCode() {
            var that = this;
            // $.getJSON("http://ip-api.com/json/?callback=?", function (data) {
            //     that.countryCode = data.countryCode;
            // });
            axios.get("https://api.ipdata.co/?api-key=78e1295abccb1616027b85a6bcb612d286aef4cdb397a2c091fd861a") // 使用eric.guo77@gmail.com 申请的code
                .then(function (response) {
                    console.log(response);
                    that.countryCode = response.data.country_code;
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        _pars(obj) {
            let results = [];
            for (let key in obj) {
                results.push(key + '=' + obj[key]);
            }
            return results.join('&');
        },
        getPostDetail() {
            var that = this;
            var postId = this.$util.getUrlParam('postid');
            var url = this.$util.getBaseUrl() + "api/moments/" + postId;
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                // console.log(JSON.stringify(response));
                that.moment = response.data;

                setTimeout(() => {
                    // var div = document.getElementById("code");
                    // div.dataset.clipboardText = that.shareid;
                    that.getComments(response.data.MomentGuid, response.data.Id);
                    that.getAllmonents(response.data.Owner.Id);

                    that.paramsForApp = that._pars({
                        postid: postId,
                        userid: response.data.Owner.Id,
                        postguid: response.data.MomentGuid
                    });

                    if (response.data.Videos.length > 0) {
                        // console.log(response.data.Videos[0].ThumbnailUrl);
                        playByWowzaPlayer(response.data.Videos[0].DownloadUrl, response.data.Videos[0].ThumbnailUrl, 'tweebaaPlayerElement');
                    } else if (that.moment.ReP && that.moment.ReP.Vs.length > 0) {
                        playByWowzaPlayer(that.moment.ReP.Vs[0].D, that.moment.ReP.Vs[0].T, 'tweebaaPlayerElementReP');
                    }

                    mySwiper = new window.Swiper('.swiper-container', {
                        direction: 'horizontal',
                        loop: false,
                        initialSlide: 0,
                        pagination: {
                            el: '.swiper-pagination',
                            type: 'bullets'
                        }
                    });
                    // document.getElementById('swiper').style.display = 'none';

                    var url = that.$util.getBaseUrl() + "api/v2/analytics";
                    var pram = {
                        AnalyticsItemTypeId: 1,
                        AnalyticsActionTypeId: 1,
                        PostId: postId,
                        CountryCode: that.countryCode,
                        StaySeconds: Math.floor(Math.random() * (120 - 10)) + 10
                    };
                    that.$util.getAxois(url, "POST", pram).then(function (response) {
                        //console.log(JSON.stringify(response.data));
                    }).catch(function (error) {
                        console.log(JSON.stringify(error) + '#######################');
                    });
                    that.picWidth = document.getElementById("testWidth").offsetWidth;
                    that.picWidth66 = document.getElementById("testWidth66").offsetWidth;
                });
            }).catch(function (error) {
                console.log(JSON.stringify(error));
            });

            function playByWowzaPlayer(url, img, id) {
                var myPlayer = window.WowzaPlayer.create(id, {
                    "license": that.$util.getL(),
                    "title": "",
                    "description": "",
                    "sourceURL": url,
                    "autoPlay": true,
                    "volume": "75",
                    "mute": false,
                    "loop": false,
                    "audioOnly": false,
                    "uiShowQuickRewind": true,
                    "uiQuickRewindSeconds": "15",
                    "posterFrameURL": img
                });

                function startBeachBall() {
                    console.log("Starting BeachBall");
                    // document.getElementById("loading").innerHTML = "<br /><i class='fa fa-spinner fa-spin'></i>";
                }

                function stopBeachBall() {
                    console.log("Stopping BeachBall");
                    var $loading = document.getElementById("loading");
                    if ($loading) {
                        $loading.innerHTML = '';
                    }
                }

                // Called when play requested
                function onPlayCallback(event) {

                    if (event.eventID === "onAssetPlayerStarted") {
                        startBeachBall();

                        // Listen for playback starting
                        myPlayer.onPlayheadTime(onPlayheadTimeCallback);
                    }
                }


                function onPlayheadTimeCallback(event) {
                    if (event.time > 0) {
                        // Video is streaming
                        stopBeachBall();

                        // Unregister as listener
                        myPlayer.removeOnPlayheadTime(onPlayheadTimeCallback);
                    }
                }

                that.videoReady = true;
                myPlayer.onPlay(onPlayCallback);
            }


        },
        openPic(index) {
            //console.log(index);
            mySwiper.slideTo(index, 0, false);
            this.$refs.swiperBox.style.zIndex = 1001;
            this.$refs.swiperBox.style.opacity = 1;
        },
        hidePic() {
            this.$refs.swiperBox.style.zIndex = -1;
            this.$refs.swiperBox.style.opacity = 0;
        },
        getComments(momentGuid, momentId) {
            var that = this;
            var paramStr = {
                "PostGuid": momentGuid,
                "PostId": momentId
            };
            var url = this.$util.getBaseUrl() + "/api/v2/post/comments";
            this.$util.getAxois(url, "POST", paramStr).then(function (response) {
                //console.log(JSON.stringify(response.data));
                let comments = response.data;
                if (comments.length > 0) {
                    comments = comments.slice(0, 5);
                    comments.map((item) => {
                        if (item.SubComments && item.SubComments.length> 0) {
                            let SubComments = item.SubComments.slice(0, 5);
                            item.SubCommentsNew = SubComments.reverse();
                            for (var j = 0, lenJ = item.SubCommentsNew.length; j < lenJ; j++) {
                                var itemInner = item.SubCommentsNew[j];
                                var Comment = itemInner.Comment;
                                var index = Comment.indexOf('  ');
                                itemInner.Comment = Comment.substring(index + 2);
                            }
                        }
                    });
                    that.comments = comments;
                }
            }).catch(function (error) {
                console.log(JSON.stringify(error));
            });
        },
        getAllmonents(ownerId) {
            var that = this;
            var url = this.$util.getBaseUrl() + "/api/v2/moments";
            var paramStr = {
                "ownerId": ownerId,
                "currentpage": 0,
                "itemsperpage": 3,
                "ShowOnHomePage": true,
            };
            this.$util.getAxois(url, "POST", paramStr).then(function (response) {
                //console.log(JSON.stringify(response.data));
                that.momentsData = response.data.Data;
            }).catch(function (error) {
                console.log(JSON.stringify(error));
                //messages($.i18n.prop('systemError'), 'error', 'top', 3);
            });
        },
        openLink(link) {
            window.open(link);
        }
    }
}
